"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

export default function Sales() {
  const [formData, setFormData] = useState({
    customerName: "",
    quantity: "",
    price: "",
    date: "",
    paymentStatus: ""
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Handle form submission
    console.log(formData)
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  // Sample sales data
  const salesData = [
    {
      id: 1,
      date: "2024-01-15",
      customer: "Rahul Construction",
      quantity: 5000,
      price: 45000,
      status: "Paid"
    },
    {
      id: 2,
      date: "2024-01-14",
      customer: "SK Builders",
      quantity: 3000,
      price: 27000,
      status: "Pending"
    },
    {
      id: 3,
      date: "2024-01-13",
      customer: "Metro Projects",
      quantity: 7000,
      price: 63000,
      status: "Paid"
    }
  ]

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-4xl font-bold text-gray-900">Sales Records</h1>
        <p className="text-gray-600 mt-2">Manage your sales transactions</p>
      </div>

      {/* New Sale Form */}
      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">New Sale Entry</h2>
        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Customer Name</label>
            <Input
              name="customerName"
              value={formData.customerName}
              onChange={handleChange}
              placeholder="Enter customer name"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Quantity (pieces)</label>
            <Input
              name="quantity"
              type="number"
              value={formData.quantity}
              onChange={handleChange}
              placeholder="Enter quantity"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Price (₹)</label>
            <Input
              name="price"
              type="number"
              value={formData.price}
              onChange={handleChange}
              placeholder="Enter price"
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Date</label>
            <Input
              name="date"
              type="date"
              value={formData.date}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">Payment Status</label>
            <Input
              name="paymentStatus"
              value={formData.paymentStatus}
              onChange={handleChange}
              placeholder="Paid/Pending"
              required
            />
          </div>

          <div className="flex items-end">
            <Button type="submit" className="w-full">Add Sale Record</Button>
          </div>
        </form>
      </Card>

      {/* Sales Table */}
      <Card className="p-6">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-semibold">Sales History</h2>
          <Button variant="outline">Export CSV</Button>
        </div>
        
        <div className="rounded-md border">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Date</TableHead>
                <TableHead>Customer</TableHead>
                <TableHead>Quantity</TableHead>
                <TableHead>Price (₹)</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {salesData.map((sale) => (
                <TableRow key={sale.id}>
                  <TableCell>{sale.date}</TableCell>
                  <TableCell>{sale.customer}</TableCell>
                  <TableCell>{sale.quantity}</TableCell>
                  <TableCell>{sale.price}</TableCell>
                  <TableCell>
                    <span className={`px-2 py-1 rounded-full text-sm ${
                      sale.status === "Paid" 
                        ? "bg-green-50 text-green-500" 
                        : "bg-yellow-50 text-yellow-500"
                    }`}>
                      {sale.status}
                    </span>
                  </TableCell>
                  <TableCell>
                    <div className="flex space-x-2">
                      <Button variant="outline" size="sm">Edit</Button>
                      <Button variant="destructive" size="sm">Delete</Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </Card>
    </div>
  )
}
